.Main {
    display: flex;
    margin: 0 auto;
    background-color: #e8ebf4;
    height: 100%;
    width: 90%;
    padding-top: 20px;
}

.Main-card {
    display: grid;
    height: 200px;
    width: 30%;
    background-color: white;
    border-radius: 10px;
}

.Main-title {
    background: linear-gradient(90deg, rgba(74,40,100,1) 0%, rgba(203,61,83,1) 100%, rgba(239,67,78,1) 100%);
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-around;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}


.Main-card__icon {

    width: 30px;
    height: 30px;
    margin-left: 5px;
}

.Main-card__text {
    color: white;
}


.Main-card__dates {
    display: grid;
    justify-content: center;
    align-items: center;
    padding: 0px;
}

.Main-card__cash {
    display: flex;
    justify-content: center;
    font-size: 25px;
    color:  rgb(74,40,100);
    
}

