}

.Main-card__date {
    
    display: flex;
    font-size: small;
    justify-content: center;
    margin-top: -2px;
    color: #111e6c;
}



.Main-buttons {
  
    width: 70%;
    margin-left: 50px;
    
}

.Main-buttons__list {
    height: 40px;
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Main-buttons__list button {
    height: 25px;
    width: 150px;
    background-color: white;
    border: transparent;
    cursor: pointer;
    color: #111e6c;
    border-radius: 10px;
    font-size: 15px;
}


.Main-buttons__list button:hover {
    background-color: #969696;
}

.Main-buttons__filter {
    display: flex;
    height: 40px;
    margin-top: 10px;
    justify-content: flex-end;
}
.Main-buttons__filter button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px;
    width: 150px;
    background-color: white;
    border: transparent;
    cursor: pointer;
    
}

.filter {
    margin-left: 5px;
    height: 20px;
    width: 20px;
}